<template>
    <div>
        <div class="header">
            <a href="https://github.com/BiYuqi" target="_blank" class="logo"><img src="../../static/img/logo.png" alt=""></a>
            <i>前端实用工具</i>
            <span class="btn" @click="toShow"></span>
            <a href="http://loadingmore.com/" target="_blank" class="github-img">
                <img src="http://loadingmore.com/demo/src/img/timg.png" alt="">
            </a>
        </div>
    </div>
</template>

<script>
    import { mapActions } from 'vuex';
    export default {
        data () {
            return {

            }
        },
        methods: {
            ...mapActions(['enableshow','disableshow']),
            toShow () {
                this.$store.state.isShow.isShow === false ? this.enableshow() : this.disableshow()
            }
        }
    }
</script>
<style scoped>
    .github-img{
        width: 40px;
        height: 40px;
        display: inline-block;
        position: absolute;
        right: 40px;
        top: 30px;
    }
    .github-img img{
        width: 45px;
        height: 30px;
    }
</style>



